import {
    IntrinsicNodeProps,
    View,
    Text,
    ElementNode,
    activeElement,
    hexColor,
  } from "@lightningjs/solid";
  import styles from "./ListColumn.style";
  import { For, createEffect, createSelector, on } from "solid-js";
  import { assertTruthy } from "@lightningjs/renderer/utils";
  import { FocusRing, TileRow } from "..";
  import theme from "theme";
import { Column } from "@lightningjs/solid-primitives";
  export default function ListColumn(props: IntrinsicNodeProps) {
    let focusRingRef;
    const isFirst = createSelector(() => {
        return 0;
      });
  
    // console.log('props',props)
    return (
        <View clipping style={styles.itemsContainer}>
        <Column plinko announce="All Trending - Week" animate y={props.y} style={styles.Column}>
          <For each={props.data || []}>
            {(items, i) =>
              <TileRow autofocus={isFirst(i())}
                  items={items} onFocus={props.onFocus} onEnter={props.onEnter} />
            }
          </For>
        </Column>
      </View>
    );
  }
  