import { useLocation, useNavigate, useParams } from "@solidjs/router";
import { ElementNode, View, activeElement } from "@lightningjs/solid";
import { useFocusManager, useAnnouncer } from "@lightningjs/solid-primitives";
import Background from "../components/Background";
import Menu from "../components/Menu/Menu";
import theme from 'theme';
import { createEffect, createSignal, onMount } from "solid-js";
import styles from "../styles";
import { focusMenu, setFocusMenu } from "../state";
import { FocusRing } from "../components";
import { menuItems } from "../components/Menu/constant";

const App = (props) => {
  const announcer = useAnnouncer();
  announcer.enabled = false;
  const navigate = useNavigate();
  const location :any = useLocation();
  const params = useParams();
  let navDrawer, lastFocused;
  const [localtionActive, setLocaltionActive] = createSignal(location);

  useFocusManager({
    Menu: 'm',
    Escape: 'Escape',
  });

  onMount(() => {
    setFocusMenu(true);
  });

  createEffect(() => {
    setLocaltionActive(location.pathname);
  });

  return (
    <View
      ref={window.APP}
      onLast={() => history.back()}
      onMenu={() => navigate("/")}
      style={{ width: 1920, height: 1080 }}
      onLeft={() => {
        if (navDrawer.states.has("focus") || (focusMenu() == false && localtionActive() == '/search')) {
          return false;
        }
        lastFocused = activeElement();
        navDrawer.setFocus();
      }}
      onRight={() => {
        setFocusMenu(false);
        return navDrawer.states.has("focus") && lastFocused.setFocus()
      }}
      plinko
    >
      <Background />

      {/* <FocusRing color={hexColor(theme.color.focus)} ref={focusRingRef} /> */}
      <View style={styles.pageWrapper}>{props.children}</View>

      <Menu ref={navDrawer} mennuItems={menuItems} />
    </View>
  );
};

export default App;
