import { useNavigate } from "@solidjs/router";
import {
  View,
  Text,
  ElementNode,
  IntrinsicNodeProps,
  For,
} from "@lightningjs/solid";
import {
  menuContainerStyle,
  menuIconStyle,
  menuLineStyle,
  menuMainStyle,
} from "../../../assets/styles/menuStyle";
import { Column } from "@lightningjs/solid-primitives";
import styles from "./Menu.styles";
import theme from "theme";
import Icon from "../Icon";
import { AiOutlineMenuUnfold } from "solid-icons/ai";

interface NavButtonProps extends IntrinsicNodeProps {
  icon: string;
  children: string;
}
const basePath = import.meta.env.BASE_URL;

function NavButton(props: NavButtonProps) {
  return (
    <View {...props} forwardStates style={styles.NavButton}>
      <View
        y={-16}
        src={basePath + "assets/icon-search.png"}
        style={menuIconStyle}
        forwardStates
      >
        <View style={menuLineStyle}></View>
      </View>
      <Text
        style={{
          ...theme.typography.button1,
          fontSize: 38,
          x: 50,
          y: 16,
          height: 50,
          alpha: 0,
          active: {
            alpha: 1,
          },
        }}
      >
        {props.children}
      </Text>
    </View>
  );
}

export default function Menu(props) {
  let backdrop;
  const navigate = useNavigate();

  function onFocus(this: ElementNode) {
    this.children.forEach((c) => c.states!.add("active"));
    this.children.selected!.setFocus();
  }

  function onBlur(this: ElementNode) {
    this.selected = 0;
    this.children.forEach((c) => c.states!.remove("active"));
  }
  return (
    <View style={styles.MenuWrapper}>
      <Column
        {...props}
        onFocus={onFocus}
        onBlur={onBlur}
        style={styles.Column}
        animate
      >
        {(props?.mennuItems || []).map((menuItem) => (
          <View
            src={basePath + `${menuItem.icon}`}
            style={menuIconStyle}
            forwardStates
            onEnter={() => navigate(`${menuItem.path}`)}
          >
            <View style={styles.menuLineStyle}/>
            <Text style={styles.MenuText}>{menuItem.name}</Text>
          </View>
        ))}
      </Column>
      <View ref={backdrop}></View>
    </View>
  );
}
