import { createSignal, createSelector, For, createEffect, on } from "solid-js";

import { ElementNode, View, Text, activeElement, hexColor, setActiveElement } from '@lightningjs/solid';
import { Row } from '@lightningjs/solid-primitives';
import { useNavigate } from "@solidjs/router";
import styles from '../styles';
import { assertTruthy } from "@lightningjs/renderer/utils";
import {
  playVideo,
  closeVideo,
  backwardTime,
  forwardTime,
  upControls,
  downControls
} from '../../main-video';

export default function TiviPage(_props: any) {
  const isFirst = createSelector(() => {
    return 0;
  });
  const [backdropAlpha, setBackdropAlpha] = createSignal(0);
  const [isPlayVideo, setIsPlayVideo] = createSignal(0);
  let lastFocused;

  function onEnter(this: ElementNode) {
    let entity = this.children.selected;
    if (isPlayVideo()) {
      return;
    }
    assertTruthy(entity && entity.id);
    playVideo(entity.url);
    setBackdropAlpha(0.9);
    setIsPlayVideo(1);
    videoRef.setFocus()
    lastFocused = activeElement();
  };

  function onEscape()  {
    closeVideo();
    setBackdropAlpha(0);
    setIsPlayVideo(0);
    setTimeout(() => {
      lastFocused.setFocus();
    }, 10);
  };

  const demos = [
    {
      title: 'Streaming Test',
      id: 'vod',
      url: 'https://live-par-2-cdn-alt.livepush.io/live/bigbuckbunnyclip/index.m3u8',
      description: 'Streaming Implementation',
    },
    {
      title: 'Bunny Video',
      id: 'bunny',
      url: 'https://test-streams.mux.dev/x36xhzz/x36xhzz.m3u8',
      description: 'VoD Implementation',
    }
  ]

  function DemoTile(props) {
    const Container = {
      width: 370,
      height: 320,
      borderRadius: 6,
      scale: 1,
      color: 0x182b44ff,
      focus: {
        scale: 1.1,
        color: 0xffffffff,
      },
    };
    const [color, setColor] = createSignal(0xffffffff);

    return <View {...props} onFocus={() => setColor(0x000000ff)} onBlur={() => setColor(0xffffffff)} animate style={Container}>
      <View x={30}>
        <Text y={30} fontSize={84} color={color()}>{props.index + 1}</Text>
        <Text y={140} fontSize={42} width={340} height={42} contain="both" color={color()}>{props.title}</Text>
        <Text y={200} fontSize={28} width={330} contain="width" color={color()}>{props.description}</Text>
      </View>    
    </View>
  }

  const [rowX, setRowX] = createSignal(140);

  function onSelectedChanged(row) {
    if (isPlayVideo()) {
      videoRef.setFocus()
    }
    // slip row
    // setRowX(row.selected * -400 + 140);
  }

  let videoRef;

  createEffect(on(activeElement, (elm) => {
    if (isPlayVideo()) {
      videoRef.setFocus()
    }
  }, { defer: true}))

  const videoLeft = () => {
    if (isPlayVideo()) {
      backwardTime();
    }
  }

  const videoRight = () => {
    if (isPlayVideo()) {
      forwardTime();
    }
  }

  const videoRUp = () => {
    if (isPlayVideo()) {
      upControls();
    }
  }

  const videoDown = () => {
    if (isPlayVideo()) {
      downControls();
    }
  }

  return (
    <View colorTop={0x446b9eff} colorBottom={0x2c4f7cff} onEscape={onEscape}>
      <View x={120}>
        <Text fontSize={90} x={110} y={40}>Videos</Text>
        <View y={140} height={1} width={1800} color={0xe8d7f9ff} />

      </View>
      <Row onSelectedChanged={onSelectedChanged} onEnter={onEnter}
        animationSettings={{delay: 20, duration: 300}}
        animate x={rowX()} y={300} width={4400} style={styles.Row} justifyContent={'flexStart'} gap={40}>
        <For each={demos}>
            {(demo, i) =>
              <DemoTile skipFocus={isPlayVideo()} autofocus={isFirst(i())} index={i()} {...demo}/>
            }
        </For>
        <View
          y={240}
          height={10}
          width={10}
          color={0xe8d7f9ff}
          onLeft={videoLeft}
          onRight={videoRight}
          onUp={videoRUp}
          onDown={videoDown}
          ref={videoRef}
          skipFocus
        />
      </Row>
      <View alpha={backdropAlpha()} color={hexColor('#000000')}  zIndex={200} />
    </View>
  );
}
