import { render, Canvas, Config } from '@lightningjs/solid';
import { HashRouter, Route } from "@solidjs/router";
import App from './pages/App';
import coreExtensionModuleUrl from './AppCoreExtensions.js?importChunkUrl';
import coreWorkerUrl from './threadx-core-worker.js?importChunkUrl';
import HomePage from './pages/Home';
import SearchPage from './pages/Search';
import VideoPage from './pages/Video';
import TiviPage from './pages/Tivi';
import NotFound from './pages/NotFound';
import Videos from './pages/Videos';
import Portal from './pages/Portal';

Config.debug = false;
Config.fontSettings.fontFamily = 'Ubuntu';
Config.fontSettings.color = 0xffffffff;

const driver = 'main' as 'main' | 'threadx';

render(() =>  (
  <Canvas options={{
    coreExtensionModule: coreExtensionModuleUrl,
    threadXCoreWorkerUrl: driver === 'threadx' ? coreWorkerUrl : undefined,
    deviceLogicalPixelRatio: 0.5,
    appWidth: 1920, appHeight: 1080
  }}>
    <HashRouter root={App}>
      <Route path="/" component={HomePage} />
      <Route path="/search" component={SearchPage} />
      <Route path="/videos" component={Videos} />
      <Route path="/tv" component={TiviPage} />
      <Route path="/*all" component={NotFound} />
      <Route path="browse" component={Videos} />
      <Route path="examples" component={Portal} />
    </HashRouter>
  </Canvas>
));
